.todoListMain .header input {
    padding: 10px;
    font-size: 16px;
    border: 2px solid black;
    width: 165px;
    text-align: center;
  }
  .todoListMain .header button {
    padding: 10px;
    font-size: 16px;
    margin: 10px;
    margin-right: 0px;
    background-color: #0066FF;
    color: #FFF;
    border: 2px solid #0066FF;
  }
  .todoListMain .header button:hover {
    background-color: #003399;
    border: 2px solid #003399;
    cursor: pointer;
  }
  .todoListMain .theList {
    list-style: none;
    padding-left: 0;
    width: 250px;
    display: block;
    margin-left: auto;
    margin-right: auto
  } 
  .todoListMain .theList li {
    color: #333;
    background-color: rgba(255,255,255,.5);
    padding: 15px;
    margin-bottom: 15px;
    border-radius: 5px;
    -webkit-transition: background-color .2 ease-out;
    -o-transition: background-color .2 ease-out;
    transition: background-color .2 ease-out;
  }
  ul.theList {
    padding: 0;
  }

  .todoListMain .theList li:hover {
    background-color: black;
    cursor: pointer;
    color: white;
  }

  form {
      text-align: center
  }


